/* Global styles and variables */
:root {
  --money-green: #0C4B33;
  --gold: #B4975A;
  --cream: #F7F3E9;
  --dark-brown: #2C1810;
  --light-brown: #8B7355;
  --error-red: #802A2A;
  --success-green: #1B4D3E;
}

body {
  color: var(--dark-brown);
  font-family: "Playfair Display", Georgia, serif;
}

.app {
  background: url('https://www.transparenttextures.com/patterns/paper-fibers.png');
  min-height: 100vh;
}

.container {
  display: grid;
  grid-template-columns: 1fr 1.5fr;
  grid-gap: 20px;
  padding: 20px;
  max-height: 90vh;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.95);
  border: 2px solid var(--gold);
  border-radius: 36px;
  box-shadow: 4px 4px 0 var(--money-green);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

h1 {
  color: var(--money-green);
  font-size: 2em;
  text-transform: uppercase;
  letter-spacing: 2px;
  margin-bottom: 20px;
  border-bottom: 2px solid var(--gold);
  padding-bottom: 15px;
  text-align: center;
}

h2 {
  color: var(--money-green);
  font-size: 1.5em;
  margin-bottom: 15px;
}

/* Budget info styling */
.budget-info {
  padding: 20px;
  border: 1px solid var(--gold);
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.95);
}

.budget-details p {
  font-size: 1.2em;
  margin: 15px 0;
  color: var(--dark-brown);
  font-family: "Libre Baskerville", Georgia, serif;
}

.under-budget {
  color: var(--success-green);
  font-weight: bold;
}

.over-budget {
  color: var(--error-red);
  font-weight: bold;
}

/* Button styling */
.reset-budget, button[type="submit"] {
  background-color: var(--money-green);
  color: var(--cream);
  border: 1px solid var(--gold);
  padding: 12px 25px;
  font-family: "Playfair Display", serif;
  font-size: 1em;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.reset-budget:hover, button[type="submit"]:hover {
  background-color: var(--gold);
  color: var(--money-green);
  box-shadow: 2px 2px 0 var(--money-green);
}

/* Form styling */
.form__group {
  margin-bottom: 15px;
}

.form__field {
  font-family: "Libre Baskerville", Georgia, serif;
  width: 100%;
  border: none;
  border-bottom: 2px solid var(--light-brown);
  outline: 0;
  font-size: 1.1em;
  color: var(--dark-brown);
  padding: 7px 0;
  background: transparent;
  transition: all 0.3s ease;
}

.form__label {
  font-family: "Playfair Display", serif;
  color: var(--light-brown);
  font-size: 1em;
}

.form__field:focus {
  border-bottom: 2px solid var(--gold);
}

.form__field:focus ~ .form__label {
  color: var(--money-green);
  font-weight: 600;
}

/* Table styling */
.expense-list table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
}

.expense-list thead {
  position: sticky;
  top: 0;
  background-color: var(--money-green);
  z-index: 1;
}

.expense-list th {
  background-color: var(--money-green);
  color: var(--cream);
  padding: 15px 20px;
  font-family: "Playfair Display", serif;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.expense-list td {
  padding: 15px 20px;
  border-bottom: 1px solid var(--light-brown);
  font-family: "Libre Baskerville", Georgia, serif;
}

.expense-list tr:hover {
  background-color: var(--cream);
}

.expense-list tr:last-child td {
  border-bottom: none;
}

/* Empty state styling */
.expense-list p {
  text-align: center;
  padding: 30px;
  color: var(--light-brown);
  font-style: italic;
  border: 1px dashed var(--gold);
  border-radius: 36px;
  margin: 20px 0;
  font-family: "Libre Baskerville", Georgia, serif;
}

/* Additional decorative elements */
.add-expense {
  padding: 20px;
  border: 1px solid var(--gold);
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.95);
  height: fit-content;
}

.add-expense::before, .set-budget::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  right: -10px;
  bottom: -10px;
  background-color: var(--cream);
  border-radius: 36px;
  z-index: -1;
}

/* Add these styles for the first page */
.set-budget {
  position: relative;
  padding: 40px;
  border: 1px solid var(--gold);
  border-radius: 36px;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
  max-width: 500px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.set-budget form {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.set-budget h2 {
  margin-bottom: 30px;
  color: var(--money-green);
  font-size: 2em;
  text-align: center;
  width: 100%;
}

/* Optional: Add some decorative elements */
.set-budget::before {
  content: "";
  position: absolute;
  top: 10px;
  left: 10px;
  right: -10px;
  bottom: -10px;
  background-color: var(--cream);
  border-radius: 36px;
  z-index: -1;
}

/* Star Button styling */
.star-button {
  position: relative;
  padding: 12px 35px;
  background: var(--gold);
  font-size: 17px;
  font-weight: 500;
  color: var(--money-green);
  border: 3px solid var(--gold);
  border-radius: 36px;
  box-shadow: 0 0 0 #b4975a8c;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  font-family: "Playfair Display", serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  overflow: visible;
}

.star-1, .star-2, .star-3, .star-4, .star-5, .star-6 {
  position: absolute;
  height: auto;
  filter: drop-shadow(0 0 0 var(--cream));
  z-index: -5;
  transition: all 1s cubic-bezier(0.05, 0.83, 0.43, 0.96);
  pointer-events: none;
}

/* Individual star positions and sizes */
.star-1 {
  top: 20%;
  left: 20%;
  width: 25px;
}

.star-2 {
  top: 45%;
  left: 45%;
  width: 15px;
}

.star-3 {
  top: 40%;
  left: 40%;
  width: 5px;
}

.star-4 {
  top: 20%;
  left: 40%;
  width: 8px;
}

.star-5 {
  top: 25%;
  left: 45%;
  width: 15px;
}

.star-6 {
  top: 5%;
  left: 50%;
  width: 5px;
}

.star-button:hover {
  background: transparent;
  color: var(--gold);
  box-shadow: 0 0 25px #b4975a8c;
}

/* Hover animations for each star */
.star-button:hover .star-1 {
  top: -80%;
  left: -30%;
  filter: drop-shadow(0 0 10px var(--cream));
  z-index: 2;
}

.star-button:hover .star-2 {
  top: -25%;
  left: 10%;
  filter: drop-shadow(0 0 10px var(--cream));
  z-index: 2;
}

.star-button:hover .star-3 {
  top: 55%;
  left: 25%;
  filter: drop-shadow(0 0 10px var(--cream));
  z-index: 2;
}

.star-button:hover .star-4 {
  top: 30%;
  left: 80%;
  filter: drop-shadow(0 0 10px var(--cream));
  z-index: 2;
}

.star-button:hover .star-5 {
  top: 25%;
  left: 115%;
  filter: drop-shadow(0 0 10px var(--cream));
  z-index: 2;
}

.star-button:hover .star-6 {
  top: 5%;
  left: 60%;
  filter: drop-shadow(0 0 10px var(--cream));
  z-index: 2;
}

.fil0 {
  fill: var(--cream);
}

/* Scrollbar Styling */
.expense-list::-webkit-scrollbar {
  width: 8px;
}

.expense-list::-webkit-scrollbar-track {
  background: var(--cream);
  border-radius: 4px;
}

.expense-list::-webkit-scrollbar-thumb {
  background: var(--gold);
  border-radius: 4px;
}

/* First page specific styling */
.initial-container {
  max-width: 500px !important;
  min-height: 600px !important;
  display: flex !important;
  flex-direction: column !important;
  justify-content: center !important;
  padding: 3rem !important;
  background: rgba(255, 255, 255, 0.97) !important;
}

.set-budget {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 3rem;
  width: 100%;
}

.budget-intro {
  text-align: center;
  width: 100%;
}

.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 1rem;
}

.logo h1 {
  font-size: 2.5rem;
  color: var(--money-green);
  margin: 0;
  padding: 0;
  border: none;
  letter-spacing: 4px;
}

.logo-star {
  color: var(--gold);
  font-size: 1.5rem;
}

.tagline {
  color: var(--gold);
  font-size: 1.2rem;
  font-style: italic;
  margin-bottom: 1rem;
}

.decorative-line {
  height: 2px;
  background: linear-gradient(
    to right,
    transparent,
    var(--gold),
    transparent
  );
  width: 100%;
  margin: 1rem 0;
}

.budget-form {
  width: 100%;
  max-width: 350px;
  text-align: center;
}

.budget-form h2 {
  color: var(--money-green);
  font-size: 1.8rem;
  margin-bottom: 2rem;
  font-weight: 600;
}

.form__group {
  margin-bottom: 2.5rem;
  position: relative;
}

.form__field {
  width: 100%;
  padding: 0.8rem;
  font-size: 1.3rem;
  text-align: center;
  border: 2px solid var(--gold);
  border-radius: 8px;
  background: transparent;
  color: var(--money-green);
  transition: all 0.3s ease;
}

.form__field:focus {
  outline: none;
  border-color: var(--money-green);
  box-shadow: 0 0 10px rgba(180, 151, 90, 0.2);
}

.form__label {
  position: absolute;
  top: -25px;
  left: 0;
  width: 100%;
  text-align: center;
  font-size: 1rem;
  color: var(--dark-brown);
  transition: all 0.3s ease;
}

.budget-footer {
  text-align: center;
  width: 100%;
}

.footer-text {
  color: var(--light-brown);
  font-size: 1.1rem;
  letter-spacing: 2px;
}

/* Star Button specific styling for first page */
.set-budget .star-button {
  margin-top: 1rem;
  padding: 1rem 2.5rem;
  font-size: 1.1rem;
  letter-spacing: 2px;
}

/* Remove arrows from number input */
.form__field::-webkit-outer-spin-button,
.form__field::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.form__field[type=number] {
  -moz-appearance: textfield;
}

/* Optional: Add subtle animation */
@keyframes fadeIn {
  from { opacity: 0; transform: translateY(20px); }
  to { opacity: 1; transform: translateY(0); }
}

.set-budget > * {
  animation: fadeIn 0.8s ease-out forwards;
}

.budget-intro { animation-delay: 0.2s; }
.budget-form { animation-delay: 0.4s; }
.budget-footer { animation-delay: 0.6s; }
/* Optional: Add a decorative element */
.initial-container::before {
  content: "★";
  font-size: 2em;
  color: var(--gold);
  display: block;
  margin-bottom: 20px;
}